// LoadModels.tsx
import * as THREE from "three";
import React, { useMemo, useEffect } from "react";
import { useGLTF } from "@react-three/drei";
import { scenePath } from "@/app/Routing";
import { ModelData } from "@/interfaces/CoreInterfaces";

const LoadModels: React.FC<{ modelData: ModelData[] }> = ({ modelData }) => {
  return (
    <group dispose={null}>
      {modelData.map((model, i) => (
        <LoadModel key={i} {...model} />
      ))}
    </group>
  );
};

export default LoadModels;

export function LoadModel(modelData: ModelData) {
  const path = scenePath + modelData.path;
  const { scene } = useGLTF(path);

  // Create a wrapper group that applies the scene's global transform
  // and then resets the scene's transform so its children remain in the correct world position.
  const wrapper = useMemo(() => {
    const wrapperGroup = new THREE.Group();

    // Copy the top-level transform from the GLTF scene
    wrapperGroup.position.copy(scene.position);
    wrapperGroup.rotation.copy(scene.rotation);
    wrapperGroup.scale.copy(scene.scale);

    // Reset the scene's own transform so that its children are positioned relative to the wrapper
    scene.position.set(0, 0, 0);
    scene.rotation.set(0, 0, 0);
    scene.scale.set(1, 1, 1);

    // Add the scene (its children) to the wrapper
    wrapperGroup.add(scene);

    return wrapperGroup;
  }, [scene]);

  // Enable shadows on all meshes within the scene
  useEffect(() => {
    scene.traverse((child) => {
      if ((child as THREE.Mesh).isMesh) {
        child.castShadow = true;
        child.receiveShadow = true;
      }
    });
  }, [scene]);

  return <primitive object={wrapper} dispose={null} />;
}
