import React, { createContext, useContext, useState } from "react";
import { PointOfInterest } from "@/interfaces/CoreInterfaces";

interface PoiContextProps {
  activePoi: PointOfInterest;
  previousPoi: PointOfInterest | null; // Store the previous POI
  setActivePoi: (poi: PointOfInterest) => void;
}

const PoiContext = createContext<PoiContextProps | undefined>(undefined);

export const PoiProvider: React.FC<{ initialPoi: PointOfInterest; children: React.ReactNode }> = ({ initialPoi, children }) => {
  const [activePoi, setActivePoiState] = useState<PointOfInterest>(initialPoi);
  const [previousPoi, setPreviousPoi] = useState<PointOfInterest | null>(null);

  const setActivePoi = (poi: PointOfInterest) => {
    setPreviousPoi(activePoi); // Store the current POI as the previous one
    setActivePoiState(poi);
  };

  return <PoiContext.Provider value={{ activePoi, previousPoi, setActivePoi }}>{children}</PoiContext.Provider>;
};

export const usePoi = () => {
  const context = useContext(PoiContext);
  if (!context) {
    throw new Error("usePOI must be used within a POIProvider");
  }
  return context;
};
