import React from "react";
import * as THREE from "three";
import { useThree } from "@react-three/fiber";
import { DebugSettings, CameraSettings } from "@/interfaces/CoreInterfaces";

interface AddCameraTargetProps {
  cameraSettings?: CameraSettings;
  debugSettings?: DebugSettings;
}

const AddCameraTarget: React.FC<AddCameraTargetProps> = ({ cameraSettings, debugSettings }) => {
  const { scene } = useThree();

  let cameraFocusObject: THREE.Object3D | null = scene.getObjectByName("cameraFocusObject") as THREE.Object3D;

  if (!cameraFocusObject) {
    cameraFocusObject = new THREE.Object3D();
    cameraFocusObject.name = "cameraFocusObject";
    if (cameraSettings?.lookAt) {
      cameraFocusObject.position.set(cameraSettings.lookAt.x, cameraSettings.lookAt.y, cameraSettings.lookAt.z);
    }

    if (debugSettings?.allTrue) {
      const geometry = new THREE.SphereGeometry(0.05, 32, 32);
      const material = new THREE.MeshBasicMaterial({ color: 0xff0000 });
      const debugSphere = new THREE.Mesh(geometry, material);
      cameraFocusObject.add(debugSphere);
    }

    scene.add(cameraFocusObject);
  }

  return null;
};

export default AddCameraTarget;
