import React, { CSSProperties } from "react";
import { Card, CardContent, CardMedia, Typography, CardActions, Button, useTheme, useMediaQuery } from "@mui/material";
import { scenePath } from "@/app/Routing";

const InfoWindow: React.FC<{
  title?: string;
  description?: string[];
  image?: string;
  parentStyling?: CSSProperties;
  globalPath?: boolean;
  imageStyling?: CSSProperties;
  buttons?: string[];
}> = ({ title, description, image, parentStyling, globalPath = false, imageStyling, buttons }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));
  const imagePath = globalPath ? image : `${scenePath}/images/${image}`;

  return (
    <Card
      sx={{
        display: "flex",
        flexDirection: "column",
        maxWidth: isMobile ? "100%" : "345px",
        height: "auto",
        backgroundColor: "rgba(0, 0, 0, 0.8)",
        pointerEvents: "auto",
        borderRadius: isMobile ? "0.25rem 0.25rem 0 0" : "0.5rem",
        ...parentStyling,
      }}
    >
      {!isMobile && image && <CardMedia component="img" sx={{ ...imageStyling }} image={imagePath} alt={title} />}
      <CardContent sx={{ flex: 1 }}>
        {title && (
          <Typography variant="h5" component="div" sx={{ fontFamily: "Red Hat Display", fontWeight: 600, color: "white", paddingBottom: 1 }}>
            {title}
          </Typography>
        )}
        {description &&
          description.map((desc, index) => (
            <Typography key={index} variant="body2" sx={{ fontFamily: "Red Hat Display", fontWeight: 400, color: "lightgray", paddingTop: 1 }}>
              {desc}
            </Typography>
          ))}
      </CardContent>
      {isMobile && image && <CardMedia component="img" sx={{ ...imageStyling }} image={imagePath} alt={title} />}
      {buttons && (
        <CardActions>
          {buttons.map((button, index) => (
            <Button key={index} size="small" sx={{ color: "white" }}>
              {button}
            </Button>
          ))}
        </CardActions>
      )}
    </Card>
  );
};

export default InfoWindow;
